*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.main{
  width: 100%;
  height: 100vh;
  background-color: #393053;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main > h1{
  color: white;
  padding: 50px;
  font-size: 3.5rem;
}



.faq_center{
  width: 40%;
  min-height: 30%;
  background-color: #5d508a;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.qna_main{
  width: 100%;
  height: 50px;
  background-color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  text-align: justify;
  cursor: pointer;
  margin-top: 5px;
overflow: hidden;
transition: 0.5s;

}

.qna_main:first-child{
  margin: 0;
}

.active{
  height:  170px;
}

.qna_main > h4{
  padding-bottom:10px;
  height: 50px;
}

.qna_main p{
 cursor: text;
}




